.title {
  background-color: #f9e79f;
  color: #6e2c00;
  text-align: center;
  padding: 20px;
}

.alumnos-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-top: 20px;
}

.alumno-card {
  background-color: #fefefe;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 300px;
  text-align: center;
}

.alumno-card h3 {
  color: #333;
}

.alumno-card p {
  font-size: 16px;
  color: #555;
}

.status {
  padding: 10px;
  color: #fff;
  border-radius: 5px;
  margin-top: 10px;
}

.aprobado {
  background-color: #28a745;
}

.reprobado {
  background-color: #dc3545;
}
